.App {
  text-align: center;
}

.App-header {
  background-color: #282c34; /* Keep this for a consistent header background */
  min-height: 100vh; /* Full height to cover the screen */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; /* Text color for header */
}

/* Link styles */
.App-link {
  color: #61dafb; /* Link color */
}

/* Material-UI Tabs styles */
.MuiTabs-indicator {
  background-color: white !important; /* Underline color for active tab */
}

.MuiTab-root.Mui-selected {
  color: white !important; /* Text color for active tab */
  font-weight: bold; /* Make the text bold */
}

/* Bounce animation keyframes */
@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

/* Apply bounce animation class */
.bounce {
  animation: bounce 2s infinite; /* Infinite bounce animation */
}
