body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

h2 {
  animation: fadeInUp 1s ease-in-out forwards; /* Animation for h2 */
}

h4 {
  animation: fadeInUp 1s ease-in-out forwards; /* Animation for h4 */
  animation-delay: 0.5s; /* Delay for h4 to start after h2 */
}

body p {
  animation: fadeInUp 1s ease-in-out forwards; /* Add to paragraphs */
  animation-delay: 1s; /* Delay for paragraphs */
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 600px) {
   h2, h4 {
       animation-duration: 0.8s; /* Shorter duration on smaller screens */
   }
}

h2:hover, h4:hover {
   color: #61dafb; /* Change color on hover */
   transition: color 0.3s ease; /* Smooth transition */
}
